import "./App.scss";

import React from "react";
import { Helmet } from 'react-helmet';
import interpolateString from 'src/actions/string-interpolator';

import favicon from "src/assets/favicon.ico";

class App extends React.Component {

  render(){
    let { 
      children, orgName, repoName, currentVersion, contentTitle, 
      isTagged, frontmatter = {}, information = {} 
    } = this.props;
    let { page_title = "", page_description = "" } = frontmatter;
    let { title = "", version = "" } = information;
    if (contentTitle !== "") {
      contentTitle += " | "
    }  
    let boilerplateDefaultDesc = `This is an automatically generated document site of version '${currentVersion}' for ${repoName} in ${orgName}. EOSIO Developer Docs.`;
    let boilerplateDefaultTitle = `${contentTitle} ${repoName} | ${orgName}`;
    let pageMetaTitle = "";
    let pageMetaDescription = "";

    if (Object.keys(frontmatter).length > 0) {
      pageMetaTitle = (page_title) ? interpolateString(page_title, this.props) : boilerplateDefaultTitle;
      pageMetaDescription = (page_description) ? interpolateString(page_description, this.props) : boilerplateDefaultDesc
    } else {
      pageMetaTitle = (title) ? `${title} | API Specification` : boilerplateDefaultTitle;
      pageMetaDescription = `API Specification ${version} generated using Swagger`;
    }
    
    if (!isTagged)
    return (
      <div className="App">
        <Helmet
          titleTemplate="%s | EOSIO Developer Docs"
          defaultTitle="EOSIO Developer Docs"
        >
          <html lang="en" />

          <title>{pageMetaTitle}</title>
          <meta charSet="utf-8" />
          <meta httpEquiv="x-ua-compatible" content="ie=edge" />
          <meta
            name="viewport"
            content="width=device-width,initial-scale=1,shrink-to-fit=no,viewport-fit=cover"
          />

          <meta name="description" content={pageMetaDescription} />

          <link rel="icon" type="image/png" href={favicon} />

          <script type="text/javascript">{`setTimeout(() => window.dataLayer.push({ 'organization': '${orgName}', 'repository': '${repoName}', 'version': '${currentVersion}' }), 50);`}</script>
        </Helmet>
        {children}
      </div>    )
    else
    return (
      <div className="App">
        <Helmet
          titleTemplate="%s | EOSIO Developer Docs"
          defaultTitle="EOSIO Developer Docs"
        >
          <html lang="en" />

          <title>{pageMetaTitle}</title>
          <meta charSet="utf-8" />
          <meta httpEquiv="x-ua-compatible" content="ie=edge" />
          <meta
            name="viewport"
            content="width=device-width,initial-scale=1,shrink-to-fit=no,viewport-fit=cover"
          />

          <meta name="description" content={pageMetaDescription} />

          <link rel="icon" type="image/png" href={favicon} />

          <script type="text/javascript">{`setTimeout(() => window.dataLayer.push({ 'organization': '${orgName}', 'repository': '${repoName}', 'version': '${currentVersion}' }), 50);`}</script>
        </Helmet>
        {children}
      </div>
    )
  }

}

export default App
