const interpolateString = function (string, props) {
  let { orgName, repoName, rawTag, currentVersion } = props;
  return string.replace(/{org}|{repo}|{tag}/gi, (term) => {
    switch(term) {
      case "{org}":
        return orgName;
      case "{repo}":
        return repoName;
      case "{tag}":
        return (rawTag) ? rawTag : currentVersion;
      default:
        return term;
    }
  })
}

export default interpolateString;
