import "./DocSearch.scss";

import React, { Component } from "react";

import config from "../../../../gatsby-config.js";

const HITS_PER_PAGE = 10;

class DocSearch extends Component {
  initialize(props) {
    let { ALGOLIA_INDEX_NAME, ALGOLIA_APP_ID, ALGOLIA_SEARCH_KEY } = config.siteMetadata;
    let {
      isCollection, isTagged, searchPath, searchVersion
    } = props;
    let algoliaOptions = {
      'hitsPerPage': HITS_PER_PAGE,
      'facetFilters': []
    }

    if (this.algoliaInitialized) {
      return;
    }

    if (isCollection) {
      if (searchPath === '/' || searchPath === '') {
        algoliaOptions.facetFilters.push("tags:root");
      } else {
        algoliaOptions.facetFilters.push("path:" + searchPath);
        algoliaOptions.facetFilters.push("version:" + searchVersion);
      }
    } else if (isTagged) {
      algoliaOptions.facetFilters.push("version:" + searchVersion);
    }


    this.algoliaInstance = window.docsearch({
      appId: ALGOLIA_APP_ID,
      apiKey: ALGOLIA_SEARCH_KEY,
      indexName: ALGOLIA_INDEX_NAME,
      inputSelector: "#algolia-doc-search",
      debug: process.env.NODE_ENV === "development",
      algoliaOptions: algoliaOptions
    });

    this.algoliaInitialized = true;
  }

  loadAlgoliaJS(props) {
    this.initialize(props);
  }

  componentDidMount() {
    this.loadAlgoliaJS(this.props);
  }

  componentDidUpdate() {
    let {
      isCollection, isTagged, searchPath, searchVersion
    } = this.props;
    let facetFilters = []

    if (isCollection) {
      if (searchPath === '*') {
        facetFilters.push("*");
      } else if (searchPath === '/' || searchPath === '') {
        facetFilters.push("tags:root");
      } else {
        facetFilters.push("path:"+searchPath);
        facetFilters.push("version:"+searchVersion);
      }
    } else if (isTagged) {
      facetFilters.push("version:"+searchVersion);
    }

    if (this.algoliaInitialized)
      this.algoliaInstance.algoliaOptions.facetFilters = facetFilters;
  }

  render () {

    let { ALGOLIA_INDEX_NAME, ALGOLIA_APP_ID, ALGOLIA_SEARCH_KEY } = config.siteMetadata;

    return ALGOLIA_APP_ID && ALGOLIA_SEARCH_KEY && ALGOLIA_INDEX_NAME ? (
      <form
        className="DocSearch"
        id="DocSearch"
        onMouseOver={() => this.loadAlgoliaJS(this.props)}
        onClick={() => this.loadAlgoliaJS(this.props)}
        onSubmit={e => e.preventDefault()}
      >
        <input
          id="algolia-doc-search"
          type="text"
          placeholder="Search"
          aria-label="Search"
          required
        />
        <button className="close-key" type="reset"></button>
      </form>
  ) : null;
  }
}

export default DocSearch;
